@font-face {
  font-family: "Gilroy-Medium";
  src: url("./gilroy-medium/gilroy-medium.eot"); /* IE 9 Compatibility Mode */
  src: url("./gilroy-medium/gilroy-medium.eot?#iefix")
      format("embedded-opentype"),
    /* IE < 9 */ url("./gilroy-medium/gilroy-medium.woff2") format("woff2"),
    /* Super Modern Browsers */ url("./gilroy-medium/gilroy-medium.woff")
      format("woff"),
    /* Firefox >= 3.6, any other modern browser */
      url("./gilroy-medium/gilroy-medium.ttf") format("truetype");
  /* Safari, Android, iOS */
  font-display: swap;
}
