@keyframes rotateUp {
  from {
    transform: ratate(0);
  }
  to {
    transform: rotate(-90deg);
  }
}
@keyframes rotateLeft {
  from {
    transform: rotate(-90deg);
  }
  to {
    transform: rotate(-180deg);
  }
}
@keyframes rotateDown {
  from {
    transform: ratate(-180deg);
  }
  to {
    transform: rotate(-270deg);
  }
}
@keyframes rotateRight {
  from {
    transform: ratate(-270deg);
  }
  to {
    transform: rotate(-360deg);
  }
}
.moveRightMouse {
  left: 555px;
  transition: left 8s cubic-bezier(0, 0, 1, 1);
}

.rotateMouseUp {
  left: 555px;
  animation: rotateUp 4s infinite alternate;
}

.moveUpMouse {
  transform: rotate(-90deg);
  left: 555px;
  bottom: 150px;
  transition: bottom 4s cubic-bezier(0, 0, 1, 1);
}

.rotateMouseLeft {
  transform: rotate(-90deg);
  left: 555px;
  bottom: 150px;
  animation: rotateLeft 4s infinite alternate;
}

.moveLeftMouse {
  transform: rotate(-180deg);
  left: 0px;
  bottom: 150px;
  transition: left 8s cubic-bezier(0, 0, 1, 1);
}

.rotateMouseDown {
  transform: rotate(-180deg);
  left: 0px;
  bottom: 150px;
  animation: rotateDown 4s infinite alternate;
}

.moveDownMouse {
  transform: rotate(-270deg);
  left: 0px;
  bottom: 0px;
  transition: bottom 4s cubic-bezier(0, 0, 1, 1);
}

.rotateMouseRight {
  transform: rotate(-270deg);
  left: 0px;
  bottom: 0px;
  animation: rotateRight 4s infinite alternate;
}

