.dm-modal:hover {
  transition: all 0.6s;
  transform: scale(1.1);
}

.close {
  position: absolute;
  top: 5px;
  right: 5px;
  z-index: 9999;
  float: right;
  width: 25px;
  height: 25px;
  color: #686767;
  text-align: center;
  text-decoration: none;
  line-height: 21px;
  cursor: pointer;
}

.close:after {
  display: block;
  border: 1px solid #000000;
  border-radius: 50%;
  content: "X";
  transition: all 0.6s;
  transform: scale(0.85);
}

.close:hover:after {
  transform: scale(1);
}

